export const TableHead = { // 需要传进来一个this对象
  data() {
    return {
      tableColumn: [{
        type: 'index',
        title: '序号',
        width: 50
      },
      {
        title: '商品编号',
        field: 'goods_id',
        width: 80
      },
      {
        title: '商品名称',
        field: 'goods_name',
        width: 400
      },
      {
        title: '年周/批次',
        field: 'batch',
        width: 88
      },
      {
        title: '生产日期',
        field: 'production_time',
        width: 88
      },
      {
        title: '单位',
        field: 'unit_name',
        width: 88
      },
      {
        title: '入库数量',
        field: 'goods_num',
        width: 70
      },
      {
        title: '采购价',
        field: 'purchase_price',
        formatter: 'priceFormat',
        headerAlign: "right", // 表头对齐方式
        align: "right", // 列对齐方式
        width: 100
      },
      {
        title: '小计金额',
        field: 'subtotal_price',
        formatter: 'priceFormat',
        headerAlign: "right", // 表头对齐方式
        align: "right", // 列对齐方式
        width: 100
      },
      {
        title: '仓库',
        field: 'warehouse_name',
        width: 100
      },
      {
        title: '货位',
        field: 'allocation_name',
        width: 130
      },
      {
        title: '其他费用',
        field: 'other_price',
        headerAlign: "right", // 表头对齐方式
        align: "right", // 列对齐方式
        formatter: 'priceFormat',
        width: 100
      },
      {
        title: '备注',
        field: 'remark'
      }
      ]
    }
  },
  methods: {
    footerMethod({
      columns,
      data
    }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 2) {
            return "页小计";
          }
          if (["goods_num", "subtotal_price", "other_price"].includes(column.property)) {
            var num = this.$utils.sum(data, column.property);
            if (column.property === "goods_num") {
              return num
                .toString()
                .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
            }
            if (column.property === "subtotal_price") {
              return num
                .toFixed(2)
                .toString()
                .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
            }
            if (column.property === "other_price") {
              return num
                .toString()
                .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
            }
            return Number.isInteger(num) ? num : num.toFixed(2);
          }
          return null;
        })
      ]
    },
    getTableData({
      data
    }) {
      if (data.code === 200) {
        this.TableData = data.data
      }
    }
  }
}