<template>
  <div class="seePage">
    <!-- 库存入库列表查看 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>库存管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">其他入库管理</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>查看其他入库单</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item label="库存组织：" size="mini">
          <span>{{TableData.instoragList.company_name}}</span>
        </el-form-item>
        <el-form-item label="单据号：" size="mini">
          <p>{{TableData.instoragList.instorage_sn}}</p>
        </el-form-item>
        <el-form-item label="业务日期：" size="mini">
          <span>{{TableData.instoragList.business_time}}</span>
        </el-form-item>
        <el-form-item label="仓库名称：" size="mini">
          <span>{{TableData.instoragList.warehouse_name}}</span>
        </el-form-item>
        <el-form-item label="入库类型：" size="mini">
          <span>{{TableData.instoragList.type_name}}</span>
        </el-form-item>
        <el-form-item label="供应商(厂家)：" size="mini">
          <span>{{TableData.instoragList.supplier_name}}</span>
        </el-form-item>
        <el-form-item label="部门：" size="mini">
          <span>{{TableData.instoragList.department_name}}</span>
        </el-form-item>
        <el-form-item label="采购员：" size="mini">
          <span>{{TableData.instoragList.buyer_name}}</span>
        </el-form-item>
        <el-form-item label="分摊方式：" size="mini">
          <span>{{TableData.instoragList.other_price_type_name}}</span>
        </el-form-item>
        <el-form-item label="付款方式：" size="mini">
          <span>{{TableData.instoragList.receivables_mode_name}}</span>
        </el-form-item>
        <el-form-item label="其他费用：" size="mini">
          <span>{{TableData.instoragList.other_price}}</span>
        </el-form-item>
        <el-form-item label="关联单据号：" size="mini">
          <span>{{TableData.instoragList.order_sn}}</span>
        </el-form-item>
        <el-form-item label="厂家发货单号：" size="mini">
          <span>{{TableData.instoragList.factorysend_sn}}</span>
        </el-form-item>
        <el-form-item label="厂家发货日期：" size="mini">
          <span>{{TableData.instoragList.factorysend_time}}</span>
        </el-form-item>
        <el-form-item label="打印次数：" size="mini">
          <span>{{TableData.instoragList.print_times}}</span>
        </el-form-item>
        <el-form-item label="审核状态：" size="mini">
          <span>{{TableData.instoragList.auditor_status_name}}</span>
        </el-form-item>
      </el-form>
    </div>
    <div class="table_container">
      <xTable :ajax='{ url: "otherStorageInfoInStorage", params: {instorage_id:$route.query.instorage_id},result:"data.data.goodsList" }' :isPage="false" ref='xTable' :sign="indexSign" :footerMethod='footerMethod' :tableColumn='tableColumn' @getData='getTableData' />
      <div class="refundauditFoot">
        <div style="margin: 15px 0;">
          <el-input placeholder="请输入" v-model="TableData.instoragList.remark" maxlength="300" disabled>
            <template slot="prepend">备注</template>
          </el-input>
        </div>
        <span>创建人：{{TableData.instoragList.add_user_name }}</span>
        <span>创建时间：{{TableData.instoragList.add_time}}</span>
        <span>审核人：{{TableData.instoragList.auditor_name }}</span>
        <span>审核时间：{{TableData.instoragList.auditor_time }}</span>
        <span>修改人：{{TableData.instoragList.update_user_name }}</span>
        <span>修改时间：{{TableData.instoragList.update_time }}</span>

      </div>
    </div>
    <div class="dialog-footer" style="text-align:center;margin-top:20px;">
      <template v-if="$route.query.after_auditor_print_status == 1">
        <el-button @click="printSheet" type="primary" v-if="authority.b_print==1 && TableData.instoragList.auditor_status==2">打印</el-button>
      </template>
      <!-- <template v-else>
				<el-button @click="printSheet" type="primary"  v-if="authority.b_print==1">打印</el-button>
			</template> -->
      <template v-if="TableData.instoragList.auditor_status == 2 && authority.b_nopass==1">
        <el-button plain @click="AUDIT_GET()">弃审</el-button>
      </template>

      <template v-if="TableData.instoragList.auditor_status == 1">
        <el-button type="primary" @click="audit('otherStorageAuditorPassInStorage')" v-if="authority.b_pass==1">审核</el-button>
        <el-button @click="audit('otherStorageAuditorInvalidInStorage')" v-if="authority.b_invalid==1">作废</el-button>
      </template>
      <!-- 作废 -->
      <template v-if="TableData.instoragList.auditor_status == 3 || TableData.instoragList.auditor_status == 2 || (authority.b_invalid != 1 && authority.b_pass != 1)">
        <el-button @click="goBack">返回</el-button>
      </template>
      <el-button @click="goBack" v-else>取消</el-button>
    </div>
    <errorDialog :errorData="trunErrTable" :errorState="errorState" :titleName='[
            {field: "serial", title: " 行号/单据号",width: 200},
						{ field: "msg", title: "失败原因"}]'></errorDialog>
    <!-- 报错提示 -->
    <div v-if="printingStatusGet">
      <lodopDelivery ref="lodopDelivery" :title="title"></lodopDelivery>
    </div>
  </div>
</template>

<script>
import lodopDelivery from "common/components/public/PublicPrint.vue"; //其它入库单打印
import { TableHead } from "./config/instorageSee.js";
import { mapActions, mapGetters } from "vuex";
import errorDialog from "common/components/plugin/eldialog/errorDialog.vue";
export default {
  name: "instorageOtheninstorageInstorageSee",
  mixins: [TableHead],
  data() {
    return {
      authority: {},
      indexSign: {},
      trunErrTable: {},
      title: "",
      errorState: false,
      TableData: {
        instoragList: {},
        goodsList: []
      },
      chaid: false
    };
  },
  components: {
    lodopDelivery,
    errorDialog
  },
  computed: {
    ...mapGetters(["printingStatusGet"])
  },
  methods: {
    ...mapActions(["printingStatus"]),
    goBack() {
      this.$router.push({
        path: "/inventory/otheninstorage"
      });
    },
    printSheet() {
      this.printingStatus(true);
      this.$set(this, "title", "otherStoragePrint");
      this.$nextTick(() => {
        this.$refs.lodopDelivery.printPdf({
          instorage_id: this.$route.query.instorage_id
        });
      });
    },
    AUDIT_GET() {
      this.$baseData
        .checkCarDoctor({
          bill_type: this.TableData.instoragList.type,
          bill_id: this.$route.query.instorage_id,
          operation_type: 5
        })
        .then(() => {
          this.$axios({
            method: "post",
            url: this.$api.otherStorageUnAuditorPass,
            data: {
              instorage_id: this.$route.query.instorage_id,
              is_list: 2
            }
          }).then(res => {
            if (res.data.code == 200) {
              let errData = res.data.data; //报错
              if (errData.length) {
                let checkArry = [];
                errData.forEach(item => {
                  checkArry.push(Number(item.serial));
                });
                let activeColor = this.TableData.goodsList.filter(
                  val => checkArry.includes(val.serial_number) == true
                );
                this.indexSign = {
                  arr: activeColor,
                  str: "serial_number"
                };
                this.trunErrTable = {
                  data: errData,
                  msg: res.data.msg
                };
                this.errorState = true;
              } else {
                let msg = res.data.msg ? res.data.msg : errData;
                this.$message({
                  message: msg,
                  type: "success"
                });
                this.goBack();
              }
            }
          });
        });
    },
    audit(api) {
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: this.TableData.instoragList.type,
          bill_id: this.$route.query.instorage_id,
          operation_type: api === "otherStorageAuditorPassInStorage" ? 4 : 6
        })
        .then(() => {
          this.$axios({
            method: "post",
            url: this.$api[api],
            data: {
              instorage_id: this.$route.query.instorage_id
            }
          }).then(res => {
            if (res.data.code == 200) {
              this.$message({
                message: res.data.msg ? res.data.msg : res.data.data,
                type: "success"
              });
              this.goBack();
            }
          });
        });
    }
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
  }
};
</script>

